import {PostPropsType} from "../components/Profile/MyPosts/Post/Post";
import {Dispatch} from "redux";
import {profileAPI} from "../api/api";

const ADD_POST = "samurai-network/post/ADD-POST";
const SET_USER_PROFILE = "samurai-network/post/SET_USER_PROFILE";
const SET_USER_STATUS = "samurai-network/post/SET_USER_STATUS";
const DELETE_POST = "samurai-network/post/DELETE_POST";

let initialState = {
    posts: [
        {
            id: 1,
            message: 'Hi, how are you?',
            likeQuantity: 12,
            avatar: 'data:image/jpeg;base64,'
        },
        {
            id: 2,
            message: 'It is my first post',
            likeQuantity: 20,
            avatar: 'data:image/jpeg;base64,'
        },

        {
            id: 3,
            message: 'It is my second post',
            likeQuantity: 14,
            avatar: 'data:image/jpeg;base64,'
        },

    ],
    profile: null,
    status: ""
}

export type PostActionsType = ReturnType<typeof addPostActionCreator>
    | ReturnType<typeof setUserProfile>
    | ReturnType<typeof setUserStatus>
    | ReturnType<typeof deletePostAC>

export const addPostActionCreator = (newPostText: string) => {
    return {
        type: ADD_POST,
        newPostText
    } as const
}


export const setUserProfile = (profile: any) => {
    return {
        type: SET_USER_PROFILE,
        profile: profile
    } as const
}

export const deletePostAC = (postId: number) => {
    return {
        type: DELETE_POST,
        postId,
    } as const
}

export const setUserStatus = (status: string) => {
    return {
        type: SET_USER_STATUS,
        status: status
    } as const
}


const postsReducer = (state = initialState, action: PostActionsType) => {

    switch (action.type) {
        case ADD_POST: {
            const newPost: PostPropsType = {
                id: new Date().getTime(),
                message: action.newPostText,
                likeQuantity: 0,
                avatar: 'data:image/jpeg;base64,'
            }
            return {
                ...state,
                message: action.newPostText,
                posts: [...state.posts, newPost],
                newPostText: ''
            }
        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }
        case SET_USER_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case DELETE_POST: {
            return {
                ...state,
                posts: state.posts.filter(p => p.id !== action.postId)
            }
        }
        default:
            return (
                state
            );
    }
};

export default postsReducer;

export const getUserProfile = (userId: number) => async (dispatch: Dispatch) => {
    const data = await profileAPI.getUserProfile(userId)
    dispatch(setUserProfile(data))
}

export const getStatus = (userId: number) => async (dispatch: Dispatch) => {
    const data = await profileAPI.getStatus(userId)
    dispatch(setUserStatus(data))
}

export const updateStatus = (status: string) => async (dispatch: Dispatch) => {
    const data = await profileAPI.updateStatus(status)
    if (data.resultCode === 0) {
        dispatch(setUserStatus(status))
    }
}
